'use client';

import Image from 'next/image';
import { useEffect, useState } from 'react';

interface HeroProps {
  title: string;
  description: string;
  imageMobile: string;
  imageDesktop: string;
}

export const Hero = ({ title, description, imageMobile, imageDesktop }: HeroProps)=> {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const checkSize = () => setIsMobile(window.innerWidth < 1024);
    checkSize();
    window.addEventListener('resize', checkSize);
    return () => window.removeEventListener('resize', checkSize);
  }, []);

  return (
    <section className="relative w-full min-h-screen text-white bg-primary overflow-hidden">
      {/* Background Image */}
      <div className="absolute inset-0 z-0">
        <Image src={isMobile ? imageMobile : imageDesktop} alt="Hero Background" fill className="object-cover object-center" priority />
      </div>

      {/* Content */}
      <div className="relative z-10 layout py-20 flex flex-col justify-end gap-6 text-center laptop:text-left laptop:items-start">
        <h1 className="text-3xl laptop:text-5xl font-bold">{title}</h1>
        <p className="text-sm laptop:text-base max-w-md laptop:max-w-xl text-gray-300">{description}</p>
        <div className="flex flex-wrap justify-center laptop:justify-start gap-3">
          <button className="bg-primary border-[#262626] text-white px-5 py-2 rounded-md flex items-center gap-2">▶ Play Now</button>
          <div className="flex gap-3 items-center">
            <button className="icon-btn">+</button>
            <button className="icon-btn">❤️</button>
            <button className="icon-btn">↻</button>
          </div>
        </div>
      </div>
    </section>
  );
}
